// 
// type:1:,2,3
function  getHistory(data, type) {
  let url = "";
  if (type == "sp") {
    url = "/purSpBillInfo/query/selectBillHistory";
  } else if (type == "ma") {
    url = "/purMaBillInfo/query/selectBillHistory";
  } else if (type == "map") {
    url = "/purMapBillInfo/query/selectBillHistory";
  }
  return request({
    url: url,
    method: "get",
    params: data
  });
}

