function  upload(data) {
  return request.post("/purchasfile/api/uploadFile", data, {
    headers: { "Content-Type": "multipart/form-data" }
  });
}

// 
function  delFile(data) {
  return request.post("/purchasfile/api/deleteFile/" + data);
}
