import request from '../../../request'

// 
function  getWarnList(data) {
  return request({
    url: '/txFileOperationInfo/query/getFileOperationInfoWarnByPage',
    method: 'get',
    params: data
  });
}
//  
function  getDeptListByCompId(data) {
  return request({
    url: '/staff/query/deptListByCompId',
    method: 'get',
    params: { compId: data }
  });
}
export default{
  getWarnList,
  getDeptListByCompId

}



