// 
import request from "../../request";

// **********************************************

// 
function voyageNumber(data) {
  return request({
    url: "/oilBunkeringReport/query/voyageNumber",
    method: "get",
    params: data
  });
}

// 
function billNo(data) {
  return request({
    url: "/oilBunkeringReport/query/billNo",
    method: "get",
    params: data
  });
}

// one
function one(data) {
  return request({
    url: "/oilFeedback/query/one",
    method: "get",
    params: data
  });
}

// page
function page(data) {
  return request({
    url: "/oilFeedback/query/page",
    method: "get",
    params: data
  });
}

// save
function save(data) {
  return request({
    url: "/oilFeedback/api/save",
    method: "post",
    data
  });
}

// detail
function findDetail(data) {
  return request({
    url: "/oilFeedback/query/findDetail",
    method: "get",
    params: data
  });
}

// submit
function submit(data) {
  return request({
    url: "/oilFeedback/api/submit",
    method: "post",
    data
  });
}

// confirm
function confirm(data) {
  return request({
    url: "/oilFeedback/api/confirm",
    method: "post",
    data
  });
}

// commitLandbased
function commitLandbased(data) {
  return request({
    url: "/oilFeedback/api/commitLandbased",
    method: "post",
    data
  });
}

// approval
function approval(data) {
  return request({
    url: "/oilFeedback/api/approval",
    method: "post",
    data
  });
}

// return
function qualityReturn(data) {
  return request({
    url: "/oilFeedback/api/return",
    method: "post",
    data
  });
}

// deprecated
function deprecated(data) {
  return request({
    url: "/oilFeedback/api/deprecated",
    method: "post",
    data
  });
}

// 
function add(data) {
  return request({
    url: "/oilFeedbackTmpl/api/add",
    method: "post",
    data
  });
}

// 
function edit(data) {
  return request({
    url: "/oilFeedbackTmpl/api/edit",
    method: "post",
    data
  });
}

// 
function del(data) {
  return request({
    url: "/oilFeedbackTmpl/api/del",
    method: "post",
    data
  });
}

// 
function list(data) {
  return request({
    url: "/oilFeedbackTmpl/query/list",
    method: "get",
    params: data
  });
}

// 
function changeOrder(data) {
  return request({
    url: "/oilFeedbackTmpl/api/changeOrder",
    method: "post",
    data
  });
}

// 
function getBillOperateHis(data) {
  return request({
    url: "/oilFeedback/query/getBillOperateHis",
    method: "get",
    params: data
  });
}
export default {
  voyageNumber,
  billNo,
  one,
  page,
  save,
  findDetail,
  submit,
  confirm,
  commitLandbased,
  approval,
  qualityReturn,
  deprecated,
  add,
  edit,
  del,
  list,
  changeOrder,
  getBillOperateHis
}


