import request from '../request'

// - 
function  issueList(data) {
  return request({
    url: '/announcementIssue/query/list',
    method: 'get',
    params: data
  })
}

//  - 
function  issueDetail(data) {
  return request({
    url: '/announcementIssue/query/annoInfo',
    method: 'get',
    params: data
  })
}
export default{
  issueList,
  issueDetail
}



